.white {
    color: #fff;
}
.linkText  {
    cursor: pointer;
}
.signup-form h4.break{
    position: relative;
    overflow: hidden;
    color: #fff;
    width: 380px;
    margin: 20px auto;
}

.signup-form h4.break span{
	display: inline-block;
	vertical-align: baseline;
	zoom: 1;
	*display: inline;
	*vertical-align: auto;
	position: relative;
    padding: 0 20px;

    &:before, &:after {
        content: '';
        display: block;
        width: 1000px;
        position: absolute;
        top: 0.73em;
        border-top: 1px solid #fff;
        }

    &:before { right: 100%; }
    &:after { left: 100%; }
     
}

.signup-form input {
    height: 45px;
    margin-top: 12px;
    width: 340px;
    max-width: 340px;
    padding: 5px 16px;
    font-size: 16px;
    outline: none;
    color: #fff;
    border: 1px solid #fff;
    background:transparent;
    cursor: auto;
}

.signup-form button{
    background-color: rgb(102, 204, 51);
    color: white;
    width: 380px;
    height: 55px;
    margin-top: 20px;
    font-weight: 600;
    font-size: 16px;
    cursor: pointer;
    border: none;
    outline: none;
    border-radius: 3px;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 3px;
}

