body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
	font-family: 'Montserrat', sans-serif;
}

.App {
  text-align: center;
}
.container {
  max-width: 1220px;
  margin: 0 auto;
  display:block;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}
header { padding: 10px 0; }

header table{
	max-width: 1200px;
	margin: 0 auto;
}

header a.logo {
  display: inline-block;
  vertical-align: middle;
}
header a.logo img{
	max-width: 150px;
}

.App-header {
  background-color: #000;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

header .makeStyles-search-1{
  display: inline-block;
}
header .makeStyles-search-1 input{
  padding: 8px 8px 8px 56px;
  border: 1px solid #aaa;
  background: #000;
}

header .nav-menu{
  padding: 0;
  list-style: none;
  margin-left: 50px;
}
.mainNav {
  display:inline-block;
  vertical-align: middle;
}
.nav-menu li{
    display: inline-block;
    margin-left:20px;

}
.nav-menu li a{
  color: #fff;
  padding: 10px 20px;
  text-decoration: none;
}

.MuiAppBar-colorPrimary {
  background-color: #000 !important;
  padding: 0 20px;
}

.App-link {
  color: #61dafb;
}

.react-slideshow-wrapper.slide {
  width: 100%;
  overflow: hidden;
}
.react-slideshow-wrapper .images-wrap {
  display: flex;
  flex-wrap: wrap;
}
/* .react-slideshow-wrapper .images-wrap > div{
  width: 1200px !important;
} */
.slide-container {
  width: 1200px;
  margin: auto;
}

.each-slide > div {
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  height: 255px;
}

.each-slide span {
  padding: 20px;
  font-size: 20px;
  background: #efefef;
  text-align: center;
}

.each-fade {
  display: flex;
}

.each-fade .image-container {
  width: 75%;
  overflow: hidden;
}

.each-fade .image-container img {
  width: 100%;
}

.each-fade h2 {
  width: 25%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  background: #adceed;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/** Custom CSS **/

.container {
	max-width: 1200px;
}
.btn{
	color: #fff;
	border: 1px solid #fff;
	border-radius: 10px;
	padding: 10px 20px;
	text-decoration: none;
}

a.btn-rate{
	color: #77bf35;
	text-decoration: none;
}

td.blue { color: #1b75bc; }

/** Shows **/
.app-body {
	position: relative;
	float: left;
	width: 100%;
	background: #212422;
	margin-top: 50px
}

.show {
	position: relative;
	float: left;
	padding: 50px 0;
	width: 100%;
	text-align: left;
}

.show .row{
	position: relative;
	float: left;
	width: 100%;
	padding: 20px 0;
	/*padding: 30px 0;*/
}

.show h1{
	color: #fff;
	font-size: 35px;
	font-weight: normal;
	margin: 0 0 20px 0;
}

.show p{
	font-size: 16px;
	color: #fff;
	margin: 0 0 15px 0;
}

.show .show-content {
	width: 45%;
	float:left;
	padding-right: 5%;
}

.show .show-content .meta-data span{
	color: #77bf35;
	margin: 0 30px 0 0;
}


.show .video{
	position: relative;
	float: left;
	width: 50%;
}
.show .video img{
position: relative;
float: left;
width: 100%;
}

.show table td {
	color: #fff;
	padding-right: 30px
}

.show table td.blue {
	color: #1b75bc;
}

.buttons a{
	position: relative;
	float: left;
	margin-right: 20px;

}

.episodes {
	position: relative;
	float: left;
	width: 100%;

}
.episodes select{
	padding: 10px 40px 10px 10px;
	font-size: 18px;
	margin: 15px 0;
	background: #212422;
	border: 1px solid #fff;
	color: #fff;
}

.episodes .episode {
	position: relative;
	float: left;
	width: 100%;
	padding: 15px 0;
	border-top: 1px solid #fff;
}

.episodes .episode:last-child{
	border-bottom: 1px solid #fff;

}

.episodes .episode img{
	position: relative;
	float: left;
	width: 20%;
	margin-right: 15px;
}

.episodes .episode div{
	position: relative;
	float: left;
	width: 75%;
}
.episodes .episode div img.play-btn{
	width: 30px;
	vertical-align:middle;
}

.episodes .episode div h3{
	color: #fff;
	margin: 0 0 15px 0;
}

.watched {
	position: relative;
	float:left;
	margin-top: 20px;
	max-width: 1200px;
	width: 100%;
}

.watched h3 {
	color: #fff;
}

/* Dashboard */

.dashboard h3 {
	font-size:14px;
	color: #fff;
	text-align: left;
}

.sc-bdVaJa a {
    display: block;
    height: 100%;
}
/* Carousel */
.dvxQVR {
	font-size: 30px;
	color: #fff;
}

/* Sign In */
.authContainer {
	background: url(/static/media/auth-bg.6c2317a0.jpg) no-repeat;
	background-size: cover; 
	padding: 100px 0;
}
/* Signup Form */
.signup-form{
	max-width: 520px;
	background-image: -webkit-gradient(linear, left top, left bottom, from(#3a3a3a), to(#111));
	background-image: -webkit-linear-gradient(top, #3a3a3a, #111);
	background-image: linear-gradient(to bottom, #3a3a3a, #111);
	border-radius: 10px;
	margin: 50px auto;
	padding: 30px;
}

.signup-form p{
	color: #fff;
}

.signup-form h2.title {
	color: #fff;
	text-align: center;
	font-size: 30px;
	font-weight: bold;
}

.signup-form button{
	color: white;
	width: 100%;
	max-width: 380px;
	height: 50px;
	margin-top: 10px;
	font-weight: 600;
	font-size: 14px;
	cursor: pointer;
	border: medium none;
	outline: currentcolor none medium;
	border-radius: 3px;
	/*
		background-color: rgb(0, 107, 252);
		box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 3px;

	*/
}

.messageBar button {
    width: auto;
    margin-top: 0;
  }

.signup-form button.facebook{ background: #3c5896; }
.signup-form button.google { background: #dd4a3a; }
.signup-form button.submit { background: #77bf35; }

.signup-form button svg,
.signup-form button p{
	display: inline-block;
	margin-right: 10px;
}


.signup-form input.inputText{
	background: transparent;
	border: 1px solid #929493;
	color: #ffffff;
	height: 45px;
	width: 300px;
	maxWidth: 300px;
	padding: 0px 8px;
	fontSize: 16px;
	margin-bottom: 10px;
	/*outline: 'none',
	border: 'none',
	borderBottom: '2px solid rgba(0, 0, 0, .3)'*/
}

/* Reset Passoword */

.ResetPassword {
	padding-top: 100px;
}
.ResetPassword h2{
	color: #fff;
}
.reset-form {
	width: 100%;
	max-width: 520px;
	margin: 0 auto;
	padding: 20px;
	background-image: -webkit-gradient(linear, right top, left bottom, from(#474849), to(#1a1d1a));
	background-image: -webkit-linear-gradient(top right, #474849, #1a1d1a);
	background-image: linear-gradient(to bottom left, #474849, #1a1d1a);
}
.dashboard .alice-carousel__prev-btn, .dashboard .alice-carousel__next-btn {
	position: absolute;
	width: 40px;
	padding: 10px;
    top:8px;
}

.dashboard .alice-carousel__prev-btn {
	left: -15px;

}
.alice-carousel__next-btn {
	right: 0;
}

.dashboard .alice-carousel__next-btn [data-area]::after {
	content: " ";
}

.dashboard .alice-carousel__prev-btn [data-area]::after {
	content: " ";
}

.dashboard .alice-carousel__next-btn span {
    display: block;
    width: 40px;
    height: 50px;
	line-height: 50px;
	background: url(data:image/png;base64,) no-repeat scroll 0 0;
}

.dashboard .alice-carousel__prev-btn span {
    display: block;
    width:40px;
    height: 50px;
	line-height: 50px;
	background: url(data:image/png;base64,) no-repeat scroll 0 0;
}

.dashboard .slider-wrapper {
	margin-top: -40px;
}

.dashboard .animated > div > h3 {  text-align: left; }

.dashboard .slider-wrapper .slider .slider__container { padding: 0; }

@media all and (max-width: 479px){
		#root .App header.MuiAppBar-colorPrimary{
				position: relative;
				float: left;
				width: 100%;
		}

		#root .App .app-body { margin-top: 0; }

		.main{
				position: relative;
				float: left;
				margin-top: 0;
		}

		.show .container { padding: 0 15px; }
		.show .show-content,
		.show .video { width: 100%; margin-top: 10px; }
		.show .watched { display: none; width: 100%; overflow: hidden; }
		.show .buttons a{ margin: 15px; }
}

