.signup-form h4.break {
  position: relative;
  overflow: hidden;
  color: #fff;
  width: 380px;
  margin: 20px auto; }

.signup-form h4.break span {
  display: inline-block;
  vertical-align: baseline;
  zoom: 1;
  *display: inline;
  *vertical-align: auto;
  position: relative;
  padding: 0 20px; }
  .signup-form h4.break span:before, .signup-form h4.break span:after {
    content: '';
    display: block;
    width: 1000px;
    position: absolute;
    top: 0.73em;
    border-top: 1px solid #fff; }
  .signup-form h4.break span:before {
    right: 100%; }
  .signup-form h4.break span:after {
    left: 100%; }

